import { Link, useLocation } from 'react-router-dom';
import { useState, useEffect } from 'react';

const Header = () => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const location = useLocation();

  // Sayfa değiştiğinde sayfanın en üstüne kaydırma
  useEffect(() => {
    window.scrollTo(0, 0);
  }, [location.pathname]);

  const isActive = (path: string) => {
    return location.pathname === path ? 'text-turquoise' : 'text-white hover:text-turquoise';
  };

  const toggleMenu = () => {
    setIsMenuOpen(!isMenuOpen);
  };

  return (
    <header className="bg-navy py-3 fixed w-full top-0 z-50">
      <div className="max-w-screen-xl mx-auto px-4">
        <div className="flex justify-between items-center">
          <div className="flex items-center">
            <Link to="/" className="flex items-center space-x-2">
              <div
                style={{ 
                  width: '32px', 
                  height: '32px', 
                  background: '#00B4D8', 
                  color: 'white',
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  fontWeight: 'bold',
                  fontSize: '20px'
                }}
              >
                P
              </div>
              <span className="text-white font-bold text-xl">PayEra</span>
            </Link>
          </div>

          {/* Desktop Menu */}
          <nav className="hidden md:flex space-x-8">
            <Link 
              to="/" 
              onClick={() => window.scrollTo(0, 0)}
              className={`font-medium transition-colors ${isActive('/')}`}
            >
              Ana Sayfa
            </Link>
            <Link 
              to="/hakkimizda" 
              onClick={() => window.scrollTo(0, 0)}
              className={`font-medium transition-colors ${isActive('/hakkimizda')}`}
            >
              Hakkımızda
            </Link>
            <Link 
              to="/urunler" 
              onClick={() => window.scrollTo(0, 0)}
              className={`font-medium transition-colors ${isActive('/urunler')}`}
            >
              Ürünler
            </Link>
            <Link 
              to="/bayilik" 
              onClick={() => window.scrollTo(0, 0)}
              className={`font-medium transition-colors ${isActive('/bayilik')}`}
            >
              Bayilik
            </Link>
            <Link 
              to="/kariyer" 
              onClick={() => window.scrollTo(0, 0)}
              className={`font-medium transition-colors ${isActive('/kariyer')}`}
            >
              Kariyer
            </Link>
            <Link 
              to="/iletisim" 
              onClick={() => window.scrollTo(0, 0)}
              className={`font-medium transition-colors ${isActive('/iletisim')}`}
            >
              İletişim
            </Link>
            <Link 
              to="/destek" 
              onClick={() => window.scrollTo(0, 0)}
              className={`font-medium transition-colors ${isActive('/destek')}`}
            >
              Destek
            </Link>
          </nav>

          {/* Mobile Menu Button */}
          <div className="md:hidden">
            <button onClick={toggleMenu} className="text-white focus:outline-none">
              <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d={isMenuOpen ? "M6 18L18 6M6 6l12 12" : "M4 6h16M4 12h16M4 18h16"} />
              </svg>
            </button>
          </div>
        </div>

        {/* Mobile Menu */}
        {isMenuOpen && (
          <nav className="md:hidden mt-4 bg-navy text-white flex flex-col space-y-4 p-4 border-t border-gray-700">
            <Link 
              onClick={() => {
                toggleMenu();
                window.scrollTo(0, 0);
              }} 
              to="/" 
              className={`font-medium ${isActive('/')}`}
            >
              Ana Sayfa
            </Link>
            <Link 
              onClick={() => {
                toggleMenu();
                window.scrollTo(0, 0);
              }} 
              to="/hakkimizda" 
              className={`font-medium ${isActive('/hakkimizda')}`}
            >
              Hakkımızda
            </Link>
            <Link 
              onClick={() => {
                toggleMenu();
                window.scrollTo(0, 0);
              }} 
              to="/urunler" 
              className={`font-medium ${isActive('/urunler')}`}
            >
              Ürünler
            </Link>
            <Link 
              onClick={() => {
                toggleMenu();
                window.scrollTo(0, 0);
              }} 
              to="/bayilik" 
              className={`font-medium ${isActive('/bayilik')}`}
            >
              Bayilik
            </Link>
            <Link 
              onClick={() => {
                toggleMenu();
                window.scrollTo(0, 0);
              }} 
              to="/kariyer" 
              className={`font-medium ${isActive('/kariyer')}`}
            >
              Kariyer
            </Link>
            <Link 
              onClick={() => {
                toggleMenu();
                window.scrollTo(0, 0);
              }} 
              to="/iletisim" 
              className={`font-medium ${isActive('/iletisim')}`}
            >
              İletişim
            </Link>
            <Link 
              onClick={() => {
                toggleMenu();
                window.scrollTo(0, 0);
              }} 
              to="/destek" 
              className={`font-medium ${isActive('/destek')}`}
            >
              Destek
            </Link>
          </nav>
        )}
      </div>
    </header>
  );
};

export default Header;
